body {
  font-family: Arial, sans-serif;
  padding: 20px;
  background-color: #800080;  
}

.container {
  margin-top: 50px;
}

textarea {
  width: 100%;
}

.preview {
  background-color: #f9f9f9;
  padding: 20px;
  border: 2px solid #ddd;
  border-radius: 5px;
  min-height: 200px;
  margin-top: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); 
}

.preview table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px; 
}

.preview th, .preview td {
  padding: 10px; 
  border: 1px solid #ddd; 
  text-align: left; 
}

.preview th {
  background-color: #f2f2f2;
}

.preview img {
  display: block;          
  margin-left: auto;       
  margin-right: auto;      
  max-width: 100%;         
  height: auto;            
  max-width: 500px;        
  width: 100%;             
  margin: 10px 0;          
}

h3 {
  color: #333;
}